<template>
    <v-container>
        <v-app-bar
                app
                color="#FFFFFF"
                elevation="3"
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon color="primary"
                           tile
                           height="100%"
                           width="50px"
                           @click="$router.go(-1)">

                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{verifyYourPasswordText}}</v-toolbar-title>
                </v-col>
                <v-col>
                </v-col>
            </v-row>
        </v-app-bar>

        <v-card class="pa-5" style="margin-top: 60px;">
            <v-card-text class="pa-0" align="justify" style="margin-bottom: 20px">
                {{introduceYourPasswordToContinueText}}
            </v-card-text>
            <v-form v-model="validForm">
                <v-text-field
                        v-model="password"
                        :rules="[rules.required, rules.min8Char]"
                        :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="showPassword ? 'text' : 'password'"
                        :label="passwordText"
                        :hint="min8CharText"
                        counter
                        @click:append="showPassword = !showPassword"
                        required
                ></v-text-field>
            </v-form>

            <v-btn block color="primary" style="margin-top: 20px" @click="verifyPassword" :disabled="!validForm">
                {{continueText}}
            </v-btn>
        </v-card>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import Constants from "../../store/Constants";
    import {mapActions} from "vuex";

    export default {
        name: "SettingsAccountPasswordVerification",
        data() {
            return {
                validForm: false,
                snackbar: false,
                password: '',
                snackbarText: null,
                showPassword: false,
                rules: {
                    required: value => !!value || Constants.RULE_TEXT_MANDATORY_FIELD,
                    min8Char: value => (value.length >= 8 || value.length == 0) || Constants.RULE_TEXT_MIN_VALUE_CHARACTERS_PASSWORD,
                },
                passwordText: Constants.TEXT_PASSWORD,
                email: this.$store.getters['user/getEmail'],
                closeText: Constants.TEXT_CLOSE,
                continueText: Constants.TEXT_TO_CONTINUE,
                min8CharText: Constants.RULE_TEXT_MIN_VALUE_CHARACTERS_PASSWORD,
                verifyYourPasswordText: Constants.TEXT_VERIFY_YOUR_PASSWORD,
                introduceYourPasswordToContinueText: Constants.TEXT_INTRODUCE_YOUR_PASSWORD_TO_CONTINUE

            }
        },
        methods: {
            ...mapActions('user', ['login']),
            verifyPassword: function () {
                let self = this;
                let resultLogin = self.login({
                    email: self.email,
                    password: self.password
                });

                resultLogin.then(() => {
                    self.$router.push({name: 'settingsAccountEditEmail'});
                })
                    .catch(function (error) {
                        if(error == Constants.ERROR_WRONG_EMAIL_PASSWORD_COMBINATION){
                            self.snackbarText = Constants.ERROR_TEXT_WRONG_PASSWORD;
                        }
                        else{
                            self.snackbarText = error;
                        }

                        self.snackbar = true;
                    });
            }
        }
    }
</script>

<style scoped>

</style>